export default function Agreement(){
    
    const styleObj = {
        fontSize: 30
    }

    return (   
        <p style={styleObj}>
            <br/>
            <b>FLASK KULLANICI SÖZLEŞMESİ</b> <br/>
            Lütfen sitemizi kullanmadan önce bu ‘site kullanım şartları’nı okuyunuz. 
            Bu sosal medya sitesini kullanan ve paylaşım yapan kullanıcılarımız aşağıdaki şartları kabul etmiş varsayılmaktadır:
            Sitemizdeki web sayfaları ve ona bağlı tüm sayfalar theflask.com adresindeki Flask firmasının malıdır ve onun tarafından işletilir. Sizler (‘Kullanıcı’) sitede sunulan tüm hizmetleri kullanırken aşağıdaki şartlara tabi olduğunuzu, sitedeki hizmetten yararlanmakla ve kullanmaya devam etmekle; 14 yaşın üzerinde olduğunuzu, bu sözleşmeyi okuduğunuzu, anladığınızı ve sözleşmede yazan şartlarla bağlı olduğunuzu kabul etmiş sayılırsınız. <br/> <br/>
            <b className="mt-20">1. SORUMLULUKLAR</b> <br/> 
            a. Kullanıcıların tamamı, Flask'ı yalnızca hukuka uygun ve şahsi amaçlarla kullanacaklarını ve üçüncü kişinin haklarına tecavüz teşkil edecek nitelikteki herhangi bir faaliyette bulunmayacağını taahhüt eder. Flask dâhilinde yaptıkları işlem ve eylemlerindeki, hukuki ve cezai sorumlulukları kendilerine aittir. İşbu iş ve eylemler sebebiyle, üçüncü kişilerin uğradıkları veya uğrayabilecekleri zararlardan dolayı Flask'ın doğrudan ve/veya dolaylı hiçbir sorumluluğu yoktur. <br/>
            b. Flask üzerinde yayınlanan tüm metin, kod, grafikler, logolar, resimler, ses dosyaları ve kullanılan yazılımın sahibi (bundan böyle ve daha sonra "içerik" olarak anılacaktır) Flask'a ait olup, tüm hakları saklıdır. Yazılı izin olmaksızın site içeriğinin çoğaltılması veya kopyalanması kesinlikle yasaktır.<br/><br/> 
            <b className="mt-20">2. Gizli Bilgi</b> <br/>  
            a. Flask, site üzerinden kullanıcıların ilettiği kişisel bilgileri 3. Kişilere açıklamayacaktır. Bu kişisel bilgiler; e-posta adresi, şifre, ip adresi, fotoğraflar gibi kullanıcı’yı tanımlamaya yönelik her türlü diğer bilgiyi içermekte olup, kısaca ‘Gizli Bilgiler’ olarak anılacaktır. <br/>
            b. Gizli Bilgiler, ancak resmi makamlarca usulü dairesinde bu bilgilerin talep edilmesi halinde ve yürürlükteki emredici mevzuat hükümleri gereğince resmi makamlara açıklama yapılmasının zorunlu olduğu durumlarda resmi makamlara açıklanabilecektir. <br/> <br/> 
            <b className="mt-20">3. Kayıt ve Güvenlik</b> <br/>          
            a. Kullanıcı, doğru, eksiksiz ve güncel kayıt bilgilerini vermek zorundadır. Aksi halde bu Sözleşme ihlal edilmiş sayılacak ve Kullanıcı bilgilendirilmeksizin hesap kapatılabilecektir.
            Kullanıcı, site ve üçüncü taraf sitelerdeki şifre ve hesap güvenliğinden kendisi sorumludur. Aksi halde oluşacak veri kayıplarından ve güvenlik ihlallerinden veya donanım ve cihazların zarar görmesinden Flask sorumlu tutulamaz. <br/>  <br/>  
            <b className="mt-20">4. Sözleşmede Yapılacak Değişiklikler</b> <br/>  
            Firma, dilediği zaman sitede sunulan hizmetleri ve flask sözleşme şartlarını kısmen veya tamamen değiştirebilir. Değişiklikler sitede yayınlandığı tarihten itibaren geçerli olacaktır. Değişiklikleri takip etmek Kullanıcı’nın sorumluluğundadır. Kullanıcı, sunulan hizmetlerden yararlanmaya devam etmekle bu değişiklikleri de kabul etmiş sayılır.
        </p>
    );
}